.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

body {
  background-color: #3e394b;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.heading {
  font-weight: bold;
  font-size: 20px;
}

::selection {
  background-color: #0c63f5cc;
  color: white;
}

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

* {
  font-family: 'Poppins', sans-serif;
}

.input {
  border:none;
  outline: none;
  background-color: #0d0131;
  border-radius: 4px;
  padding: 12px 25px;
  margin: 12px 20px;
  width:250px;
  height: 25px;
  color: #FFF;
}

@media (max-width: 768px) {
  /* Stack items vertically on smaller screens */
  .flex-container {
    flex-direction: column;
    align-items: center;
  }

  img {
    max-width: 80%;
    height: auto;
  }

   /* Adjust card sizes */
   .card {
    width: 90%; /* Make cards take most of the width */
    height: auto;
  }

  /* Adjust text size for readability */
  .heading {
    font-size: 24px;
  }
}

